<script>
//import operations from '../operations';
import router from "../router";
import { mapState, mapActions } from 'vuex';
export default {
  name: "Login",
  components: {},
  computed: {
    ...mapState(["sesion"])
  },
  data() {
    return {
      formLoginValido: "",
      campoRequerido: [(r) => !!r || "El campo es requerido"],
      snackbar: false,
      colorSnack: "red",
      msgSanck: "Verifique los campos en rojo",
      mensaje: false,
      overlay: "",
    };
  },
  methods: {
    redirije(path) {
      console.log(path);
      router.push(path);
    },
    async loginLdap() {
      this.overlay = true;
      if (!this.$refs.form.validate()) {
        this.snackbar = true;
        this.colorSnack = "red";
        this.msgSanck = "Todos los campos son requeridos";
        this.mensaje = false;
        this.overlay = false;
        return;
      } else {
        try {
          let usr =this.sesion.usuario;
          let psw =this.sesion.contrasenia;
          const user = this.$store.getters.login(usr,psw);
          console.log("user = %o",user);
          console.log(Object.entries(user).length === 0);
          if (!(Object.entries(user).length === 0)) {
            this.sesion.nombre = user[0].nombre;
            this.sesion.rol = user[0].rol;
            this.sesion.rolId = user[0].rolId;
            this.sesion.curp = user[0].curp;
            this.$store.state.autenticado = true;
            $router.push({ name: "home" });
          } 
          else {
            this.overlay = false;
            this.snackbar = true;
            this.msgSanck = "Error : Credenciales inválidas";
            this.colorSnack = "red";
            this.$store.state.autenticado = false;
            this.mensaje = false;
          }
        } catch (error) {
          this.overlay = false;
          this.snackbar = true;
          this.msgSanck = "Error : Credenciales inválidas";
          this.colorSnack = "red";
          this.mensaje = false;
        }
      }
    },
  },
};
</script>
