import Vue from "vue";
import Router from "vue-router";
import Home from "./views/Home.vue";

Vue.use(Router);

export default new Router({
  mode: "history",
  routes: [
    {
      path: "/",
      name: "home",
      component: Home,
    },
    {
      path: "/administracion",
      name: "administracion",
      // route level code-splitting
      // this generates a separate chunk (about.[hash].js) for this route
      // which is lazy-loaded when the route is visited.
      component: () =>
        import(
          /* webpackChunkName: "administracion" */ "./views/Administracion.vue"
        ),
    },
    {
      path: "/nuevasolicitud",
      name: "nuevasolicitud",
      // route level code-splitting
      // this generates a separate chunk (about.[hash].js) for this route
      // which is lazy-loaded when the route is visited.
      component: () =>
        import(
          /* webpackChunkName: "nuevasolicitud" */ "./views/Nuevasolicitud.vue"
        ),
      props: true,
    },
    {
      path: "/solicitudes",
      name: "solicitudes",
      // route level code-splitting
      // this generates a separate chunk (about.[hash].js) for this route
      // which is lazy-loaded when the route is visited.
      component: () =>
        import(/* webpackChunkName: "solicitudes" */ "./views/Solicitudes.vue"),
    },
  ],
});
